header {
    height: 100vh;
    padding-top: 10rem;
    overflow: hidden;
}

.header__container {
    text-align: center;
    height: 100%;
    position: relative;
}

/* CTA styles */

.cta {
    margin-top: 2.5rem;
    display: flex;
    gap: 1rem;
    justify-content: center;
}

/* Header Socials */

.header__socials {
    display: flex;
    flex-direction: column;
    gap: 0.8rem;
    position: absolute;
    left: 0;
    bottom: 3rem;
    align-items: center;
}

.header__socials::after {
    content: "";
    display: block;
    width: 1px;
    height: 2rem;
    background: var(--color-primary);
}

/* Scroll Down */

.scroll__down {
    position: absolute;
    right: -2.3rem;
    bottom: 5rem;
    transform: rotate(90deg);
    font-weight: 300;
    font-size: 0.9rem;
}

/* medium devices */
@media (max-width: 1024px) {
    header {
        height: 68vh;
    }
}

/* small devices */
@media (max-width: 600px) {
    header {
        height: 80vh;
    }

    .header__socials,
    .scroll__down {
        display: none;
    }

}